import { useEffect, useState } from "react";
//import FileSaver from "file-saver";

import vssDB from "../signal_db/VssDB.json";
import androidApi from "../signal_db/AndroidAPI.json";

const ecuMap = new Map();
const pduMap = new Map();
const signalMap = new Map();

const vssSensors = [];
const vssActuactors = [];
const vssAttributes = [];

const vhalGlobals = [];
const vhalDoors = [];
const vhalMirrors = [];
const vhalSeats = [];
const vhalWheels = [];
const vhalWindows = [];
const vhalOthers = [];


const Overview = ({
	signalDB
}) => {

	const [busList, setBusList] = useState([]);
	const [ecuNo, setEcuNo] = useState(0);
	const [pduNo, setPduNo] = useState(0);
	const [sigNo, setSigNo] = useState(0);

	const [vssSignals, setVssSignals] = useState([]);

	const [vhalSignals, setVhalSignals] = useState([]);

	useEffect(() => {
		if (signalDB != null) {
			signalDB.pdus.forEach((pdu) => {
				const bus = pdu.bus;
				if (!ecuMap.has(bus)) {
					ecuMap.set(bus, new Map());
					pduMap.set(bus, new Map());
					signalMap.set(bus, new Map());
				}
	
				ecuMap.get(bus).set(pdu.sender, pdu.sender);
				pduMap.get(bus).set(pdu.name, pdu.name);
				for (const signal of pdu.signals) {
					signalMap.get(bus).set(signal.name, signal.name);
				}
			});
		}
		
		const list1 = [];
		let count = 0;
		ecuMap.forEach((innerMap, bus) => {
			list1.push(bus);
			count += innerMap.size;
		});
		list1.sort();
		setBusList(list1);
		setEcuNo(count);

		count = 0;
		pduMap.forEach((innerMap, bus) => {
			count += innerMap.size;
		});
		setPduNo(count);

		count = 0;
		signalMap.forEach((innerMap, bus) => {
			count += innerMap.size;
		});
		setSigNo(count);

		const list2 = [];
		vssSensors.length = 0;
		vssActuactors.length = 0;
		vssAttributes.length = 0;
		getVssSignalCount(vssDB.Vehicle, list2);
		setVssSignals(list2);

		const list3 = [];
		vhalGlobals.length = 0;
		vhalDoors.length = 0;
		vhalMirrors.length = 0;
		vhalSeats.length = 0;
		vhalWheels.length = 0;
		vhalWindows.length = 0;
		vhalOthers.length = 0;
		getVhalSignalCount(androidApi.android.car, list3);
		setVhalSignals(list3);
	}, 
	// eslint-disable-next-line
	[]);

	function getVssSignalCount(node, signals) {
		const children = node['children'];
		if (children) {
			Object.values(children).forEach((subnode) => {
				getVssSignalCount(subnode, signals);
			});
		}
		else {
			signals.push(node);
			if (node['type'] === "sensor") {
				vssSensors.push(node);
			}
			else if (node['type'] === "actuator") {
				vssActuactors.push(node);
			}
			else if (node['type'] === "attribute") {
				vssAttributes.push(node);
			}
		}
	}

	function getVhalSignalCount(node, signals) {
		Object.keys(node).forEach((name) => {
			const subnode = node[name];
			signals.push(subnode);

			if (subnode['area'] === "GLOBAL") {
				vhalGlobals.push(subnode);
			}
			else if (subnode['area'] === "DOOR") {
				vhalDoors.push(subnode);
			}
			else if (subnode['area'] === "MIRROR") {
				vhalMirrors.push(subnode);
			}
			else if (subnode['area'] === "SEAT") {
				vhalSeats.push(subnode);
			}
			else if (subnode['area'] === "WHEEL") {
				vhalWheels.push(subnode);
			}
			else if (subnode['area'] === "WINDOW") {
				vhalWindows.push(subnode);
			}
			else {
				vhalOthers.push(subnode);
			}
		});
	}

	return (
		<div className="w3-margin-top w3-margin-left w3-margin-right">
			<h3>COVESA VSS Overview</h3>
			<table className="w3-table-all w3-hoverable" style={{ width: "25%" }}>
				<thead>
					<tr className="w3-light-blue">
						<th>Type</th>
						<th>Count</th>
					</tr>
				</thead>
				<tbody>
					<tr>
						<td><b>Sensors</b></td>
						<td>{vssSensors.length}</td>
					</tr>
					<tr>
						<td><b>Actuators</b></td>
						<td>{vssActuactors.length}</td>
					</tr>
					<tr>
						<td><b>Attributes</b></td>
						<td>{vssAttributes.length}</td>
					</tr>
					<tr>
						<td><b>All</b></td>
						<td><b>{vssSignals.length}</b></td>
					</tr>
				</tbody>
			</table>

			<br/>
			<h3>Raw CAN Overview</h3>
			{/*<div className="w3-bar">
			<div className="w3-bar-item">
				<button style={{ width: "150px", marginBottom: "5px" }} onClick={() => {
					const jsonTxt = JSON.stringify(props.signalDB);
					var blob = new Blob([jsonTxt], {
						type: "text/plain;charset=utf-8"
					});
					FileSaver.saveAs(blob, "SignalDB.json");
				}}>
				Download DB
				</button>
			</div>
			</div>*/}
			<table className="w3-table-all w3-hoverable">
				<thead>
					<tr className="w3-light-blue">
						<th>Index</th>
						<th>Bus</th>
						<th>ECU</th>
						<th>Messages</th>
						<th>Signals</th>
					</tr>
				</thead>
				<tbody>
					{busList.map((bus, index) =>
						<Bus key={bus}
							index={index + 1}
							bus={bus}
							ecuNo={ecuMap.get(bus).size}
							msgNo={pduMap.get(bus).size}
							sigNo={signalMap.get(bus).size} />)}
					<tr>
						<td>-</td>
						<td>-</td>
						<td><b>{ecuNo}</b></td>
						<td><b>{pduNo}</b></td>
						<td><b>{sigNo}</b></td>
					</tr>
				</tbody>
			</table>

			<br/>
			<h3>Android Automotive VHAL Overview</h3>
			<table className="w3-table-all w3-hoverable" style={{ width: "25%" }}>
				<thead>
					<tr className="w3-light-blue">
						<th>Area</th>
						<th>Count</th>
					</tr>
				</thead>
				<tbody>
					<tr>
						<td><b>Global</b></td>
						<td>{vhalGlobals.length}</td>
					</tr>
					<tr>
						<td><b>Doors</b></td>
						<td>{vhalDoors.length}</td>
					</tr>
					<tr>
						<td><b>Mirrors</b></td>
						<td>{vhalMirrors.length}</td>
					</tr>
					<tr>
						<td><b>Seats</b></td>
						<td>{vhalSeats.length}</td>
					</tr>
					<tr>
						<td><b>Wheels</b></td>
						<td>{vhalWheels.length}</td>
					</tr>
					<tr>
						<td><b>Windows</b></td>
						<td>{vhalWindows.length}</td>
					</tr>
					<tr>
						<td><b>Others</b></td>
						<td>{vhalOthers.length}</td>
					</tr>
					<tr>
						<td><b>All</b></td>
						<td><b>{vhalSignals.length}</b></td>
					</tr>
				</tbody>
			</table>
		</div>
	);
}

const Bus = ({
	index, bus, ecuNo, msgNo, sigNo
}) => {
	return (
		<tr>
			<td>{index}</td>
			<td><b>{bus}</b></td>
			<td>{ecuNo}</td>
			<td>{msgNo}</td>
			<td>{sigNo}</td>
		</tr>
	);
}

export default Overview;