import { memo, useEffect, useState } from "react";
import { searchItems, sortTable } from "../common/Common.js";
import vssDB from "../signal_db/VssDB.json";
import vssAPI from "../signal_db/VssAPI.json";
import { 
	SIGNAL_TYPES,
	setSignals, subscribeSignals, unsubscribeSignals 
} from "../signal_db/WsReq.js";

/* global BigInt */


const SENDER = "SENDER";
const RECEIVER = "RECEIVER";

const COLUMNS = {
	INDEX      : 0,
	VSS_KEY    : 1,
	SIGNAL     : 3,
	INIT_VALUE : 7,
	TYPE       : 9,
	SENDER     : 12,
	RECEIVER   : 13,
};

const DEFAULT_SEARCH_ITEMS = [ COLUMNS.SIGNAL, COLUMNS.VSS_KEY ];
const EXTRA_SEARCH_ITEMS = [ SENDER, RECEIVER ];
const EXTRA_SEARCH_INDICES = [ COLUMNS.SENDER, COLUMNS.RECEIVER ];

const vssSignalMap = new Map();
const canToVssMap = new Map();
const vssToCanMap = new Map();
const vssApiMap = new Map();


const SignalConversion = ({
	canSignalMap, canSignalToPduMap
}) => {

	const [vssSignals, setVssSignals] = useState([]);

	const jsonData1 = {
		signals: [],
		channel: "vss",
		type: SIGNAL_TYPES.VSS
	};

	const jsonData2 = {
		signals: [],
		channel: "raw",
		type: SIGNAL_TYPES.RAW
	};

	useEffect(() => {
		Object.keys(vssDB).forEach((key) => {
			getVssDBSubTree(vssDB[key], key);
		});
		
		Object.keys(vssAPI).forEach((key) => {
			getVssAPISubTree(vssAPI[key], key);
		});

		const signals = [];
		vssSignalMap.forEach((vss_signal, vss_name) => {
			if (vss_signal.mapping) {
				signals.push(vss_name);
			}
		});
		signals.sort();
		setVssSignals(signals);

		setTimeout(() => {
			subscribe(signals);
		}, 100);

		return () => {
			unsubscribe();
		}
	}, 
	// eslint-disable-next-line
	[]);

	function getVssDBSubTree(node, path) {
		const children = node['children'];
		if (node['type'] === 'branch') {
			Object.keys(children).forEach((key) => {
				getVssDBSubTree(children[key], path + "." + key);
			});
		}
		else {
			node.name = path;
			if (node.default) {
				node.value = node.default;
			}
			else {
				node.value = "undefined";
			}
			vssSignalMap.set(path, node);
			const canSig = node.mapping;
			if (canSig) {
				canToVssMap.set(canSig, path);
				vssToCanMap.set(path, canSig);
			}
		}
	}
	
	function getVssAPISubTree(node, path) {
		if (!vssSignalMap.has(path)) {
			Object.keys(node).forEach((key) => {
				getVssAPISubTree(node[key], path + "." + key);
			});
		}
		else {
			if (vssToCanMap.has(path)) {
				vssApiMap.set(path, node);
			}
		}
	}

	function subscribe(signals) {
		jsonData1.signals.length = 0;
		jsonData2.signals.length = 0;

		for (const name of signals) {
			jsonData1.signals.push({
				name: name
			});

			if (vssToCanMap.has(name)) {
				const canSig = vssToCanMap.get(name);
				jsonData2.signals.push({
					name: canSig
				});
			}
		}

		subscribeSignals(jsonData1, (json) => {
			if (json.signals != null) {
				for (const signal of json.signals) {
					updateVssField(signal);
				}
			}
		});

		subscribeSignals(jsonData2, (json) => {
			if (json.signals != null) {
				for (const signal of json.signals) {
					updateCanField(signal);
				}
			}
		});
	}

	function updateCanField(signal) {
		//console.log(signal);
		let tag = document.getElementById("physical_value_field_" + signal.name);
		tag.value = signal.value;

		const rawValue = String(physToRaw(signal.value, canSignalMap.get(signal.name)));
			
		tag = document.getElementById("raw_value_field_" + signal.name);
		tag.innerText = rawValue;

		tag = document.getElementById("value_desc_" + signal.name);
		if (tag) {
			const options = tag.getElementsByTagName("option");
			for (let i = 0; i < options.length; i++) {
				if (options[i].innerText.startsWith(rawValue)) {
					tag.selectedIndex = i;
					break;
				}
			}
		}
	}

	function updateVssField(signal) {
		let tag = document.getElementById("vss_value_field_" + signal.name);
		if (tag) {
			tag.value = signal.value;
			tag = document.getElementById("vss_value_field2_" + signal.name);
			if (tag) {
				const signalNumVal = Number(signal.value);
				const allowed = vssApiMap.get(signal.name).allowed;
				for (const key of Object.keys(allowed)) {
					if (allowed[key] === signalNumVal) {
						tag.innerText = key;
					}
				}
			}
		}
		else {
			tag = document.getElementById("vss_value_combo_" + signal.name);
			if (tag) {
				const options = tag.getElementsByTagName("option");
				for (let i = 0; i < options.length; i++) {
					if (options[i].innerText === signal.value) {
						tag.selectedIndex = i;
						break;
					}
				}
			}
		}
	}

	function unsubscribe() {
		unsubscribeSignals({ channel: jsonData1.channel });
		unsubscribeSignals({ channel: jsonData2.channel });
	}
	
	function regenIndex(itemsFound) {
		for (let i = 0; i < itemsFound.length; i++) {
			let td = itemsFound[i].getElementsByTagName("td")[0];
			if (td) {
				td.innerText = i + 1;
			}
		}
	}

	return (
		<div className="w3-margin-top w3-margin-left w3-margin-right">
			<div className="w3-row-padding">
				<div className="w3-third">
					<label className="w3-text-blue">Search:</label>
					<input className="w3-input w3-border w3-padding" type="text"
						placeholder="Search for names..." id="searchTarget"
						defaultValue={""}
						onKeyUp={() => {
							const itemsFound = searchItems("searchTarget", DEFAULT_SEARCH_ITEMS, EXTRA_SEARCH_ITEMS, EXTRA_SEARCH_INDICES); 
							regenIndex(itemsFound);
						}}
					/>
				</div>
			</div>
			<table className="w3-table-all w3-hoverable w3-margin-top MyStckyHeader" id="myTable">
				<thead>
					<tr className="w3-light-blue MyStckyHeader">
						<th onClick={() => {sortTable(COLUMNS.INDEX, true);}} >
							Index
						</th>
						<th onClick={() => {sortTable(COLUMNS.VSS_KEY);}} >
							VSS Key
						</th>
						<th>
							VSS Value
						</th>
						<th onClick={() => {sortTable(COLUMNS.SIGNAL);}} >
							CAN Signal
						</th>
						<th>
							Physical Value
						</th>
						<th>
							Raw Value
						</th>
						<th>
							Value Descriptions
						</th>
						<th onClick={() => {sortTable(COLUMNS.INIT_VALUE, true);}} >
							Init Value
						</th>
						<th onClick={() => {sortTable(COLUMNS.TYPE);}} >
							Type
						</th>
						<th>
							Range
						</th>
						<th>
							Description
						</th>
						<th onClick={() => {sortTable(COLUMNS.SENDER);}} >
							Sender
						</th>
						<th>
							Receivers
						</th>
					</tr>
				</thead>
				<tbody>
					{
						vssSignals.map((name, index) => {
							const canSig = vssToCanMap.get(name);
							return(
								<EcuSignal key={name}
									index={index + 1}
									vss_name={name}
									vss_signal={vssSignalMap.get(name)}
									can_signal={canSignalMap.get(canSig)}
									can_pdu={canSignalToPduMap.get(canSig)}
									vss_api={vssApiMap.get(name)}
								/>
							);
						})
					}
				</tbody>
			</table>
		</div>
	);
}

const EcuSignal = ({
	index, vss_name, vss_signal, can_signal, can_pdu, vss_api
}) => {
	const initPhysicalValue = can_signal.initValue;
	const initRawValue = physToRaw(initPhysicalValue, can_signal);
	
	const [physicalValue, setPhysicalValue] = useState(Number(initPhysicalValue));
	
	const physical_value_field_name = "physical_value_field_" + can_signal.name;
	const raw_value_field_name = "raw_value_field_" + can_signal.name;
	const value_desc_name = "value_desc_" + can_signal.name;

	const [vss_value, setVssValue] = useState(vss_signal.value);

	const receivers = [];
	
	if (can_signal.receiverAry != null && can_signal.receiverAry.length !== 0) {
		for (const receiver of can_signal.receiverAry) {
			receivers.push(receiver);
		}
	}
	receivers.sort();

	const updateCan = (name, value) => {
		let valueToSend = value;
		if (can_signal.length <= 32) {
			valueToSend = Number(value);
		}
		
		let jsonData = {
			signals: [ 
				{ 
					name  : name, 
					value : valueToSend
				} 
			],
			type: SIGNAL_TYPES.RAW
		};
		
		setSignals(jsonData);
	}

	const updateVss = (name, value = vss_value) => {
		const valueToSend = value;
		if (vss_value !== value) {
			setVssValue(value);
		}
		
		let jsonData = {
			signals: [ 
				{ 
					name  : name, 
					value : valueToSend
				} 
			],
			type: SIGNAL_TYPES.VSS
		};
		
		setSignals(jsonData);
	}

	function getRange(signal) {
		let min = 0;
		let max;
		if (signal.length <= 32) {
			const enumSizeMax = 1 << signal.length;
			const enumSize = signal.valueDescAry.length;

			if (signal.isSigned && enumSize < enumSizeMax) {
				if (signal.length < 32) {
					max = (1 << (signal.length - 1)) - 1;
					min = -(max + 1);
				}
				else {
					max = 0x7FFFFFFF;
					min = -(0x80000000);
				}
			}
			else {
				if (signal.length < 32) {
					max = (1 << signal.length) - 1;
				}
				else {
					max = 0xFFFFFFFF;
				}
			}

			if (signal.factor !== 1) {
				max *= signal.factor;
				min *= signal.factor;
			}
			if (signal.offset !== 0) {
				max += signal.offset;
				min += signal.offset;
			}
			if (signal.factor !== 1) {
				max = Number(Number(max).toFixed(5));
				min = Number(Number(min).toFixed(5));
			}
		}
		else {
			max = 0xFFFFFFFFFFFFFFFFn;
			let div = BigInt(1 << (64 - signal.length));
			max /= div;
		}

		let range = min + " ~ " + max;

		return range;
	}

	function setRawValue(value) {
		const rawVal = String(value).split(" : ")[0];
		let physVal = Number(rawVal) * Number(can_signal.factor) + Number(can_signal.offset);
		physVal = Number(physVal.toFixed(5));
		updateCan(can_signal.name, physVal);
	}

	function renderVssValueField(vss_name, vss_signal) {
		if (vss_signal.datatype === "boolean") {
			return (
				<select 
					id={"vss_value_combo_" + vss_name}
					style={{ width: "100%" }}
					onChange={(e) => updateVss(vss_name, e.target.value) }
				>
					<option key="undefined">undefined</option>
					<option key="True">True</option>
					<option key="False">False</option>
				</select>
			);			
		}
		else if (vss_signal.allowed && vss_signal.allowed !== "-") {
			return (
				<select 
					id={"vss_value_combo_" + vss_name}
					style={{ width: "100%" }}
					onChange={(e) => updateVss(vss_name, e.target.value) }
				>
					<option key="undefined">undefined</option>
					{
						vss_signal.allowed.map((key) =>
							<option key={key}>
								{key}
							</option>
						)
					}
				</select>
			);
		}
		else {
			return (
				<>
					<input
						id={"vss_value_field_" + vss_name}
						style={{ width: "100%" }}
						type="text"
						defaultValue={vss_value}
						onChange={(e) => setVssValue(e.target.value)}
						onKeyDown={(e) => {
							if (e.key === 'Enter') {
								updateVss(vss_name);
							}
						}}>
					</input>
					{vss_api.allowed &&
						<span id={"vss_value_field2_" + vss_name} style={{color: "blue"}}>
						</span>
					}
				</>
			);
		}
	}

	return (
		<tr>
			<td>{index}</td>
			<td><b>{vss_name}</b></td>
			<td>
				{vss_name &&
					renderVssValueField(vss_name, vss_signal)
				}
			</td>
			<td><b>{can_signal.name}</b></td>
			<td>
				<input
					id={physical_value_field_name}
					style={{ width: "100%" }}
					type="text" 
					defaultValue={physicalValue}
					onChange={(e) => { setPhysicalValue(e.target.value); }}
					onKeyDown={(e) => {
						if (e.key === 'Enter') {
							updateCan(can_signal.name, physicalValue);
						}
					}}
				>
				</input>
			</td>
			<td id={raw_value_field_name}>{initRawValue}</td>
			{
				Object.keys(can_signal.valueDescAry).length > 0 ?
					<td>
						<select
							id={value_desc_name}
							style={{ width: "100%" }}
							onChange={(e) => { setRawValue(e.target.value) }}
						>
							{
								Object.keys(can_signal.valueDescAry).map((key) =>
									<option key={key}>
										{key.substring(5) + " : " + can_signal.valueDescAry[key]}
									</option>
								)
							}
						</select>
					</td>
					:
					<td>-</td>
			}
			<td>{can_signal.initValue}</td>
			<td>{can_signal.apType}</td>
			<td>{getRange(can_signal)}</td>
			<td>{can_signal.desc}</td>
			<td>{can_pdu.sender}</td>
			{
				receivers.length !== 0 ?
					<td>
						<select style={{ width: "100%" }} >
							{receivers.map((receiver) =>
								<Receiver key={can_signal.name + "_" + receiver} receiver={receiver} />
							)}
						</select>
					</td>
					:
					<td>-</td>
			}
		</tr>
	);
}

const Receiver = ({ receiver }) => {
	return (
		<option>{receiver}</option>
	);
}

function physToRaw(physVal, signal) {
	let rawVal = physVal;
	if (signal.factor > 0) {
		rawVal -= signal.offset;
		rawVal /= signal.factor;
		rawVal = Math.round(rawVal.toString());
	}
	return rawVal;
}

export default memo(SignalConversion);