import { memo, useEffect, useState } from "react";
import { sortTable } from "../common/Common.js";
import androidApi from "../signal_db/AndroidAPI.json";


const FILTER_PROP = "PROP";
const FILTER_AREA = "AREA";
const ANY = "ANY";

const VehicleAreaFilter = {
	GLOBAL : "GLOBAL",
	WINDOW : "WINDOW",
	MIRROR : "MIRROR",
	SEAT : "SEAT",
	DOOR : "DOOR",
	WHEEL : "WHEEL",
};

const VehicleArea = {
	GLOBAL : 0x01000000,
	/** WINDOW maps to enum VehicleAreaWindow */
	WINDOW : 0x03000000,
	/** MIRROR maps to enum VehicleAreaMirror */
	MIRROR : 0x04000000,
	/** SEAT maps to enum VehicleAreaSeat */
	SEAT : 0x05000000,
	/** DOOR maps to enum VehicleAreaDoor */
	DOOR : 0x06000000,
	/** WHEEL maps to enum VehicleAreaWheel */
	WHEEL : 0x07000000,
	
	MASK : 0x0f000000,
};

const COLUMNS = {
	INDEX       : 0,
	PROPERTY    : 1,
	DESC        : 2,
	ID          : 3,
	GROUP       : 4,
	AREA        : 5,
	TYPE        : 6,
	CHANGE_MODE : 7,
	ACCESS      : 8,
	UNIT        : 9,
	DATA_ENUM   : 10,
};

const EXTRA_SEARCH_ITEMS = [ FILTER_AREA, FILTER_PROP ];


const VhalProps = () => {

	const [allSignals, setAllSignals] = useState([]);

	const [vhalSignal, setVhalSignal] = useState({
		filterText : "",
		signals : [],
	});
	
	useEffect(() => {
		const signals = [];
		Object.keys(androidApi.android.car).forEach((name) => {
			const signalObj = androidApi.android.car[name];
			signalObj['name'] = name;
			signals.push(signalObj);
		});
		signals.sort();
		
		setAllSignals(signals);
		setVhalSignal({
			filterText : "Search for names...",
			signals : signals,
		});
	}, 
	// eslint-disable-next-line
	[]);
  
	const filterItems = (inputValue, filterTypes) => {
		var filter = inputValue.toUpperCase();
		var filterType = FILTER_PROP;

		if (filter.startsWith("?")) {
			let filterValid = false;
			const equalsIndex = filter.indexOf("=");
			if (equalsIndex > -1) {
				for (let i = 0; i < filterTypes.length; i++) {
					if (filter.indexOf(filterTypes[i]) > -1) {
						filterType = filterTypes[i];
						filter = filter.substring(equalsIndex + 1);
						filterValid = true;
						break;
					}
				}
			}

			if (!filterValid) {
				filter = "";
			}
		}

		if (filter.length === 0) {
			filter = ANY;
		}

		const signals = [];
		if (filterType === FILTER_PROP) {
			if (filter.length > 0) {
				if (filter === ANY) {
					for (const signal of allSignals) {
						signals.push(signal);
					}
				}
				else {
					for (const signal of allSignals) {
						if (signal.name.toUpperCase().indexOf(filter) > -1
							|| signal.description.toUpperCase().indexOf(filter) > -1) {
							signals.push(signal);
						}
					}
				}
			}
		}
		else if (filterType === FILTER_AREA) {
			if (filter.length > 0) {
				if (filter === ANY) {
					for (const signal of allSignals) {
						signals.push(signal);
					}
				}
				else {
					for (const signal of allSignals) {
						const area = VehicleArea.MASK & signal.constant;
						if (filter === VehicleAreaFilter.GLOBAL) {
							if (area === VehicleArea.GLOBAL) {
								signals.push(signal);
							}
						}
						else if (filter === VehicleAreaFilter.WINDOW) {
							if (area === VehicleArea.WINDOW) {
								signals.push(signal);
							}
						}
						else if (filter === VehicleAreaFilter.MIRROR) {
							if (area === VehicleArea.MIRROR) {
								signals.push(signal);
							}
						}
						else if (filter === VehicleAreaFilter.SEAT) {
							if (area === VehicleArea.SEAT) {
								signals.push(signal);
							}
						}
						else if (filter === VehicleAreaFilter.DOOR) {
							if (area === VehicleArea.DOOR) {
								signals.push(signal);
							}
						}
						else if (filter === VehicleAreaFilter.WHEEL) {
							if (area === VehicleArea.WHEEL) {
								signals.push(signal);
							}
						}
					}
				}
			}
		}
		
		setVhalSignal({
			filterText : inputValue,
			signals : signals,
		});
		
		return signals;
	}
	
	function changeArea(area) {
		if (area === ANY) {
			document.getElementById("searchSignal").value = area;
			filterItems(area, EXTRA_SEARCH_ITEMS);
		}
		else {
			const filter = "?area=" + area;
			document.getElementById("searchSignal").value = filter;
			filterItems(filter, EXTRA_SEARCH_ITEMS);
		}
	}
	
	function doSearch(searchTxt) {
		document.getElementById("selectArea").value = ANY;
		const itemsFound = filterItems(searchTxt, EXTRA_SEARCH_ITEMS);

		if (searchTxt.toLowerCase().indexOf("?area=") > -1) {
			if (itemsFound.length > 0 && searchTxt.length > "?area=".length) {
				const filter = searchTxt.substring("?area=".length).toUpperCase();
				const options = document.getElementById("selectArea").getElementsByTagName("option");
				for (const option of options) {
					if (option.innerText.toUpperCase() === filter) {
						document.getElementById("selectArea").value = option.innerText;
					}
				}
			}
		}
	}
   
	return (
		<div className="w3-margin-top w3-margin-left w3-margin-right">
			<div className="w3-row-padding">
				<div className="w3-third">
					<label className="w3-text-blue">Select Area:</label>
					<select className="w3-select w3-border" name="option" id="selectArea"
						defaultValue={ANY}
						onChange={(e) => {
							changeArea(e.target.value);
						}} >
						{Object.keys(VehicleAreaFilter).map((key) =>
							<option key={key}>{key}</option>
						)}
						<option key={ANY}>{ANY}</option>
					</select>
				</div>
				<div className="w3-third">
					<label className="w3-text-blue">Search:</label>
					<input className="w3-input w3-border w3-padding" type="text"
						placeholder={vhalSignal.filterText} id="searchSignal"
						defaultValue={vhalSignal.filterText}
						onFocus={(e) => {
							if ("Search for names..." === vhalSignal.filterText) {
								document.getElementById("searchSignal").value = "";
							}
						}}
						onKeyUp={(e) => {
							doSearch(e.target.value);
						}} />
				</div>
			</div>
			<table className="w3-table-all w3-hoverable w3-margin-top MyStckyHeader" id="myTable">
				<thead>
					<tr className="w3-light-blue MyStckyHeader">
						<th onClick={() => {sortTable(COLUMNS.INDEX, true);}} >Index</th>
						<th onClick={() => {sortTable(COLUMNS.PROPERTY);}}    >Property</th>
						<th onClick={() => {sortTable(COLUMNS.DESC);}}        >Description</th>
						<th onClick={() => {sortTable(COLUMNS.ID);}}          >ID</th>
						<th onClick={() => {sortTable(COLUMNS.GROUP);}}       >Group</th>
						<th onClick={() => {sortTable(COLUMNS.AREA);}}        >Area</th>
						<th onClick={() => {sortTable(COLUMNS.TYPE);}}        >Type</th>
						<th onClick={() => {sortTable(COLUMNS.CHANGE_MODE);}} >Change Mode</th>
						<th onClick={() => {sortTable(COLUMNS.ACCESS);}}      >Access</th>
						<th onClick={() => {sortTable(COLUMNS.UNIT);}}        >Unit</th>
						<th onClick={() => {sortTable(COLUMNS.DATA_ENUM);}}   >Data Enum</th>
					</tr>
				</thead>
				
				<tbody>
					{vhalSignal.signals.map((signal, index) =>
						<Signal key={signal.name}
							index={index + 1}
							signal={signal}
						/>)}
				</tbody>
			</table>
		</div>
	);
}

const Signal = ({ index, signal }) => {
	let enumIndex = 0;
	let hexString = Number(signal.constant & 0xFFFF).toString(16);
	const len = hexString.length;
	for (let i = 0; i < (4 - len); i++) {
		hexString = "0" + hexString;
	}

	return (
		<tr>
			<td>{index}</td>
			<td>
				<b>
				{ signal.hyperlink ?
				<a style={{color: "blue"}} href={signal.hyperlink} target="_blank" rel="noreferrer">{signal.name}</a>
				:
				<>{signal.name}</>
				}
				</b>
			</td>
			<td>{signal.description}</td>
			<td>0x{hexString}</td>
			<td>{signal.group}</td>
			<td>{signal.area}</td>
			<td>{signal.type}</td>
			<td>{signal.change_mode}</td>
			<td>{signal.access}</td>
			<td>{signal.unit}</td>
			<td>
				<b>{signal.data_enum}</b>
				{ signal.data_enum_list && signal.data_enum_list.length !== 0 &&
				<select style={{ width: "100%" }} >
				{signal.data_enum_list.map((valueDesc) =>
						<option key={"valueDesc_" + enumIndex++}>{valueDesc}</option>
				)}
				</select>
				}
			</td>
		</tr>
	);
}

export default memo(VhalProps);