import { memo, useEffect, useState } from "react";
import "./SignalTree.css";
import vssDB from "../signal_db/VssDB.json";
import { 
	SIGNAL_TYPES,
	setSignals,	subscribeSignals, unsubscribeSignals
} from "../signal_db/WsReq";


const VssTree = () => {

	const [selectedListItem, setSelectedListItem] = useState(null);
	const [vssSignal, setVssSignal] = useState({
		path: "Vehicle",
		name: "Vehicle",
		type: vssDB.Vehicle.type ? vssDB.Vehicle.type : "-",
		description: vssDB.Vehicle.description ? vssDB.Vehicle.description : "-",
		datatype: "-",
		default: "-",
		min: "-",
		max: "-",
		unit: "-",
		comment: vssDB.Vehicle.comment ? vssDB.Vehicle.comment : "-",
		deprecation: vssDB.Vehicle.deprecation ? vssDB.Vehicle.deprecation : "-",
		allowed: "-"
	});
	const [nodeOnMouseOver, setNodeOnMouseOver] = useState(null);
	const [signalValue, setSignalValue] = useState(null);

	const jsonData = {
		channel: "vss",
		type: SIGNAL_TYPES.VSS,
		signals: []
	};

	useEffect(() => {
		return () => {
			unsubscribeSignals({ channel: jsonData.channel });
		};
	}, 
	// eslint-disable-next-line
	[]);

	function getList(node, path) {
		const children = node['children'];
		return (
			<ul>
				{Object.keys(children).map((key) => {
					if ((children[key])['type'] === "branch") {
						return getSubTree(children[key], path + "." + key, key);
					}
					else {
						return null;
					}
				})}
				{Object.keys(children).map((key) => {
					if ((children[key])['type'] !== "branch") {
						return getSubTree(children[key], path + "." + key, key);
					}
					else {
						return null;
					}
				})}
			</ul>
		);
	}

	function getSignalCount(node) {
		let size = 0;
		const children = node['children'];
		if (children) {
			Object.values(children).forEach((subnode) => {
				size += getSignalCount(subnode);
			});
		}
		else {
			size = 1;
		}
		return size;
	}

	function getSubTree(node, path, name) {
		const children = node['children'];
		return (
			<li key={path} className="w3-tooltip" >
				{node['type'] === 'branch' ?
					<>
						<span 
							className="caret"
							onClick={(e) => handleTreeEvent(e.target, node, path, name)}
							style={{ fontWeight: 700 }}
							onMouseEnter={(e) => setNodeOnMouseOver(node)}
							onMouseLeave={(e) => setNodeOnMouseOver(null)}
						>
							{name}
						</span>
						<span>
							&nbsp;({getSignalCount(node)})
						</span>
						<span className="w3-text" style={{ color: "gray" }}>
							{nodeOnMouseOver === node && <em>&nbsp;- {node.description}</em>}
						</span>
						<ul className="nested">
							{Object.keys(children).map((key) => {
								if ((children[key])['type'] === "branch") {
									return getSubTree(children[key], path + "." + key, key);
								}
								else {
									return null;
								}
							})}
							{Object.keys(children).map((key) => {
								if ((children[key])['type'] !== "branch") {
									return getSubTree(children[key], path + "." + key, key);
								}
								else {
									return null;
								}
							})}
						</ul>
					</>
					:
					<>
						<span 
							onClick={(e) => handleSignal(e.target, node, path, name)}
							style={{ cursor: "pointer" }}
							onMouseEnter={(e) => setNodeOnMouseOver(node)}
							onMouseLeave={(e) => setNodeOnMouseOver(null)}
						>
							{name}
						</span>
						<span className="w3-text" style={{ color: "gray" }}>
							{nodeOnMouseOver === node && <em>&nbsp;- {node.description}</em>}
						</span>
					</>
				}
			</li>
		);
	}

	function handleTreeEvent(treeItem, node, path, name) {
		if (path !== 'Vehicle') {
			treeItem.parentElement.querySelector(".nested").classList.toggle("active");
			treeItem.classList.toggle("caret-down");
		}

		if (selectedListItem) {
			selectedListItem.setAttribute("style", 
				"color: black; cursor: pointer;"
				+ (vssSignal.type === "branch" ? "font-weight: 700;" : "font-weight: 500;"));
		}

		treeItem.setAttribute("style", 
			"color: blue; background-color: yellow; font-weight: 700; cursor: pointer;");

		setSelectedListItem(treeItem);
		setVssSignal({
			path: path,
			name: name,
			type: node.type ? node.type : "-",
			description: node.description ? node.description : "-",
			datatype: "-",
			default: "-",
			min: "-",
			max: "-",
			unit: "-",
			comment: node.comment ? node.comment : "-",
			deprecation: node.deprecation ? node.deprecation : "-",
			allowed: "-",
		});
	}

	function handleSignal(listItem, node, path, name) {
		if (selectedListItem) {
			selectedListItem.setAttribute("style", 
				"color: black; cursor: pointer;"
				+ (vssSignal.type === "branch" ? "font-weight: 700;" : "font-weight: 500;"));
		}
		listItem.setAttribute("style", "color: blue; background-color: yellow; font-weight: 700; cursor: pointer;");
		setSelectedListItem(listItem);

		let defaultValue = null;
		if (node.default) {
			defaultValue = String(node.default);
		}

		setVssSignal({
			path: path,
			name: name,
			type: node.type ? node.type : "-",
			description: node.description ? node.description : "-",
			datatype: node.datatype ? node.datatype : "-",
			default: defaultValue ? defaultValue : "-",
			min: node.min ? node.min : "-",
			max: node.max ? node.max : "-",
			unit: node.unit ? node.unit : "-",
			comment: node.comment ? node.comment : "-",
			deprecation: node.deprecation ? node.deprecation : "-",
			allowed: node.allowed ? node.allowed : "-"
		});

		setSignalValue("");
		
		//unsubscribeSignals({ channel: jsonData.channel });
		jsonData.signals.length = 0;
		jsonData.signals.push({ name: path });
		subscribeSignals(
			jsonData,
			(json) => {
				if (json && json.signals && json.signals.length > 0) {
					const signal = json.signals[0];
					if (signal.name === path) {
						updateVssField(signal);
					}
				}
			});
	}

	function getAllowedList() {
		if (vssSignal.allowed === "-") {
			return "-";
		}
		else {
			if (vssSignal.allowed && vssSignal.allowed.length !== 0) {
				return (
					<select style={{width: "100%"}} >
						{vssSignal.allowed.map((label) => 
							<option key={label}>{label}</option>
						)}
					</select>
				);
			}
			else {
				return "-";
			}
		}
	}

	function setVssValue(path, value=null) {
		if (value != null) {
			const jsonData = {
				signals: [
					{ 
						name  : path, 
						value : value
					}
				],
				type: SIGNAL_TYPES.VSS
			};
			//console.log(jsonData.signals[0]);
			setSignals(jsonData);
		}
	}

	function updateVssField(signal) {
		setSignalValue(signal.value);
		let tag = document.getElementById("vss-value-text");
		if (tag) {
			tag.value = signal.value;
		}
	}

	function renderVssValueField() {
		if (vssSignal.datatype === "boolean") {
			return (
				<select id="vss-value-combo"
					onChange={(e) => setVssValue(vssSignal.path, e.target.value)}
				>
					<option key="undefined" selected={signalValue !== "True" && signalValue !== "False"}>undefined</option>
					<option key="True" selected={signalValue === "True"}>True</option>
					<option key="False" selected={signalValue === "False"}>False</option>
				</select>
			);
		}
		else if (vssSignal.allowed && vssSignal.allowed !== "-") {
			return (
				<select id="vss-value-combo"
					onChange={(e) => setVssValue(vssSignal.path, e.target.value) }
				>
					<option key="undefined" selected={signalValue == null || signalValue === ""}>undefined</option>
					{
						vssSignal.allowed.map((key) =>
							<option key={key} selected={signalValue === key}>
								{key}
							</option>
						)
					}
				</select>
			);
		}
		else {
			return (
				<input 
					type="text"
					defaultValue={signalValue}
					id="vss-value-text"
					onKeyDown={(e) => {
						if (e.key === 'Enter') {
							setVssValue(vssSignal.path, e.target.value);
						}
					}}
				/>
			);
		}
	}
   
	return (
		<div className="w3-row-padding w3-margin-top">
			<div className="w3-col w3-container" style={{ width: "50%", height: "100%" }}>
				<div className="w3-card-4" style={{ padding: 16, height: "90vh", overflowY: "auto" }}>
					<ul id="myUL">
						<li key={"Vehicle"} className="w3-tooltip" >
							<span
								onClick={(e) => handleTreeEvent(e.target, vssDB.Vehicle, "Vehicle")}
								style={{ cursor: "pointer", fontWeight: 700 }}
								onMouseEnter={(e) => setNodeOnMouseOver(vssDB.Vehicle)}
								onMouseLeave={(e) => setNodeOnMouseOver(null)}
							>
								{"\u25B6"}{"Vehicle"}
							</span>
							<span>
								&nbsp;({getSignalCount(vssDB.Vehicle)})
							</span>
							<span className="w3-text" style={{ color: "gray" }}>
								{nodeOnMouseOver === vssDB.Vehicle && <em>&nbsp;- {vssDB.Vehicle.description}</em>}
							</span>
							{getList(vssDB.Vehicle, "Vehicle")}
						</li>
					</ul>
				</div>
			</div>
			<div className="w3-col w3-container MyStckyHeader" style={{width:"50%"}}>
				<div className="w3-card-4" style={{ height: "90vh", overflowY: "auto" }}>
					<header className="w3-container w3-light-blue">
						<h3><b>{vssSignal.path}</b></h3>
					</header>

					<div className="w3-container">
						<table className="w3-table">
							<tbody>
							<tr>
								<td><b>path</b></td>
								<td><b>{vssSignal.path}</b></td>
							</tr>
							<tr>
								<td><b>description</b></td>
								<td>{vssSignal.description}</td>
							</tr>
							<tr>
								<td><b>type</b></td>
								<td>{vssSignal.type}</td>
							</tr>
							<tr>
								<td><b>datatype</b></td>
								<td>{vssSignal.datatype}</td>
							</tr>
							<tr>
								<td><b>default</b></td>
								<td>{vssSignal.default}</td>
							</tr>
							<tr>
								<td><b>min</b></td>
								<td>{vssSignal.min}</td>
							</tr>
							<tr>
								<td><b>max</b></td>
								<td>{vssSignal.max}</td>
							</tr>
							<tr>
								<td><b>unit</b></td>
								<td>{vssSignal.unit}</td>
							</tr>
							<tr>
								<td><b>allowed</b></td>
								<td>{getAllowedList()}</td>
							</tr>
							<tr>
								<td><b>comment</b></td>
								<td>{vssSignal.comment}</td>
							</tr>
							<tr>
								<td><b>deprecation</b></td>
								<td>{vssSignal.deprecation}</td>
							</tr>
							{ vssSignal.type !== "branch" &&
							<tr>
								<td><b>Current Value</b></td>
								{ vssSignal.type === "branch" ?
								<td>-</td>
								:
								<td>
									{ renderVssValueField() }
								</td>
								}
							</tr>
							}
							</tbody>
						</table>
					</div>
				</div>
			</div>
		</div>
	);
}

export default memo(VssTree);