import { memo, useEffect, useState } from "react";
import "./SignalTree.css";
import rawApi from "../signal_db/RawAPI.json";
import { 
	SIGNAL_TYPES,
	setSignals, subscribeSignals, unsubscribeSignals
} from "../signal_db/WsReq";

/* global BigInt */


const ecuMap = new Map();


const SignalTree = ({
	signalMap,
	senderMap
}) => {

	const [selectedListItem, setSelectedListItem] = useState(null);
	const [canSignal, setCanSignal] = useState({
		signalKey : "Vehicle",
		signalName: "-",
		description: "-",
		valueDescAry: "-",
	});
	const [nodeOnMouseOver, setNodeOnMouseOver] = useState(null);

	const jsonData = {
		channel: "raw",
		type: SIGNAL_TYPES.RAW,
		signals: []
	};

	useEffect(() => {
		for (const ecuName of ECU_NAMES) {
			ecuMap.set(ecuName.shortName, ecuName.fullName);
		}

		return () => {
			unsubscribeSignals({ channel: jsonData.channel });
		};
	}, 
	// eslint-disable-next-line
	[]);

	function getClusterList(vehicleObj, path) {
		return (
			<ul>
				{Object.keys(vehicleObj).map((cluster) => {
					const clusterObj = vehicleObj[cluster];
					const clusterPath = path + "." + cluster;
					return (
						<li key={clusterPath} >
							<span 
								className="caret"
								onClick={(e) => handleTreeEvent(e.target, clusterPath)}
								style={{ fontWeight: 700 }}
							>
								{cluster}
							</span>
							<span>
								&nbsp;({getSignalCount(clusterObj)})
							</span>
							<ul className="nested">
								{Object.keys(clusterObj).map((ecu) =>
									getEcuList(clusterObj, clusterPath, ecu)
								)}
							</ul>
						</li>
					);
				})}
			</ul>
		);
	}

	function getEcuList(clusterObj, path, ecu) {
		const ecuPath = path + "." + ecu;
		const ecuObj = clusterObj[ecu];

		return (
			<li key={ecuPath} className="w3-tooltip" >
				<span 
					className="caret"
					onClick={(e) => handleTreeEvent(e.target, ecuPath)}
					style={{ fontWeight: 700 }}
					onMouseEnter={(e) => setNodeOnMouseOver(ecuObj)}
					onMouseLeave={(e) => setNodeOnMouseOver(null)}
				>
					{ecu}
				</span>
				<span>
					&nbsp;({getSignalCount(ecuObj)})
				</span>
				<span className="w3-text" style={{ color: "gray" }}>
					{nodeOnMouseOver === ecuObj && <em>&nbsp;- {getEcuFullName(ecu)}</em>}
				</span>
				<ul className="nested">
					{Object.keys(ecuObj).map((signal) => {
						const signalObj = ecuObj[signal];
						const signalPath = ecuPath + "." + signal;
						return (
							<li key={signalPath} >
								<span
									onClick={(e) => handleSignal(e.target, signalObj, signalPath)}
									style={{ cursor: "pointer" }}
									onMouseEnter={(e) => setNodeOnMouseOver(signalObj)}
									onMouseLeave={(e) => setNodeOnMouseOver(null)}
								>
									{signal}
								</span>
								<span className="w3-text" style={{ color: "gray" }}>
									{nodeOnMouseOver === signalObj && <em>&nbsp;- {signalObj['desc']}</em>}
								</span>
							</li>
						);
				})}
				</ul>
			</li>
		);
	}

	function getEcuFullName(shortName) {
		if (ecuMap.has(shortName)) {
			return ecuMap.get(shortName);
		}
		else {
			return "";
		}
	}

	function handleTreeEvent(treeItem, path) {
		if (path !== 'Vehicle') {
			treeItem.parentElement.querySelector(".nested").classList.toggle("active");
			treeItem.classList.toggle("caret-down");
		}

		if (selectedListItem != null) {
			selectedListItem.setAttribute("style", 
				"color: black; cursor: pointer;"
				+ (canSignal.signalName === "-" ? "font-weight: 700;" : "font-weight: 500;"));
		}

		treeItem.setAttribute("style", "color: blue; background-color: yellow; font-weight: 700; cursor: pointer;");

		setSelectedListItem(treeItem);
		setCanSignal({
			signalKey : path,
			signalName: "-",
			description: "-",
			valueDescAry: "-",
		});
	}

	function handleSignal(listItem, signalObj, path) {
		if (selectedListItem != null) {
			selectedListItem.setAttribute("style", 
				"color: black; cursor: pointer;"
				+ (canSignal.signalName === "-" ? "font-weight: 700;" : "font-weight: 500;"));
		}
		listItem.setAttribute("style", "color: blue; background-color: yellow; font-weight: 700; cursor: pointer;");
		setSelectedListItem(listItem);

		const canSignalName = signalObj["name"];

		setCanSignal({
			signalKey : path,
			signalName: canSignalName,
			description: signalObj["desc"],
			valueDescAry: signalObj["valueDescAry"],
		});

		//unsubscribeSignals({ channel: jsonData.channel });
		jsonData.signals.length = 0;
		jsonData.signals.push({ name: canSignalName });
		subscribeSignals(
			jsonData,
			(json) => {
				if (json && json.signals && json.signals.length > 0) {
					const signal = json.signals[0];
					if (signal.name === canSignalName) {
						updateCanField(signal, signalMap.get(canSignalName));
					}
				}
			});
	}

	function getValueDescription(valueDescAry) {
		if (valueDescAry === "-" 
			|| valueDescAry === ""
			|| Object.keys(valueDescAry).length === 0) {
			return "-";
		}
		return (
			<select style={{width: "100%"}} >
				{
					Object.keys(valueDescAry).map((key) => 
						<option key={key}>
							{key.substring(5) + " : " + valueDescAry[key]}
						</option>
					)
				}
			</select>
		);
	}

	function getReceivers() {
		if (canSignal.signalName === "-") {
			return "-";
		}
		else {
			const signal = signalMap.get(canSignal.signalName);
			if (signal.receiverAry != null && signal.receiverAry.length !== 0) {
				let receivers = [];
				for (const receiver of signal.receiverAry) {
					receivers.push(receiver);
				}
				receivers.sort();
				return (
					<select style={{width: "100%"}} >
						{receivers.map((receiver) => 
							<option key={receiver}>{receiver}</option>
						)}
					</select>
				);
			}
			else {
				return "-";
			}
		}
	}

	function getRange() {
		if (canSignal.signalName === "-") {
			return "-";
		}
		else {
			const signal = signalMap.get(canSignal.signalName);
			let min = 0;
			let max;
			if (signal.length <= 32) {
				const enumSizeMax = 1 << signal.length;
				const enumSize = canSignal.valueDescAry.length;

				if (signal.isSigned && enumSize < enumSizeMax) {
					if (signal.length < 32) {
						max = (1 << (signal.length - 1)) - 1;
						min = -(max + 1);
					}
					else {
						max = 0x7FFFFFFF;
						min = -(0x80000000);
					}
				}
				else {
					if (signal.length < 32) {
						max = (1 << signal.length) - 1;
					}
					else {
						max = 0xFFFFFFFF;
					}
				}

				if (signal.factor !== 1) {
					max *= signal.factor;
					min *= signal.factor;
				}
				if (signal.offset !== 0) {
					max += signal.offset;
					min += signal.offset;
				}
				if (signal.factor !== 1) {
					max = Number(Number(max).toFixed(5));
					min = Number(Number(min).toFixed(5));
				}
			}
			else {
				max = 0xFFFFFFFFFFFFFFFFn;
				let div = BigInt(1 << (64 - signal.length));
				max /= div;
			}

			let range = min + " ~ " + max;

			return range;
		}
	}

	function getProperty(property) {
		if (canSignal.signalName === "-") {
			return "-";
		}
		else {
			let propertyVal = "-";
			const signal = signalMap.get(canSignal.signalName);
			switch (property) {
			case "Sender":
				propertyVal = senderMap.get(signal);
				break;
			case "Type":
				propertyVal = signal.apType;
				break;
			case "Length":
				propertyVal = signal.length;
				break;
			case "Init Value":
				propertyVal = signal.initValue;
				break;
			case "Factor":
				propertyVal = signal.factor;
				break;
			case "Offset":
				propertyVal = signal.offset;
				break;
			case "Min":
				propertyVal = signal.min;
				break;
			case "Max":
				propertyVal = signal.max;
				break;
			default:
				break;
			}
			return propertyVal;
		}
	}

	function getSignalCount(node) {
		let size = 0;
		const key = node['key'];
		if (key) {
			size = 1;
		}
		else {
			Object.values(node).forEach((subnode) => {
				size += getSignalCount(subnode);
			});
		}
		return size;
	}

	return (
		<div className="w3-row-padding w3-margin-top">
			<div className="w3-col w3-container" style={{ width: "50%", height: "100%" }}>
				<div className="w3-card-4" style={{ padding: 16, height: "90vh", overflowY: "auto" }}>
					<ul id="myUL">
						<li key={"Vehicle"} >
							<span
								onClick={(e) => handleTreeEvent(e.target, "Vehicle")}
								style={{ cursor: "pointer", fontWeight: 700 }}
							>
								{"\u25B6"}{"Vehicle"}
							</span>
							<span>
								&nbsp;({getSignalCount(rawApi.Vehicle)})
							</span>
							{
								getClusterList(rawApi.Vehicle, "Vehicle")
							}
						</li>
					</ul>
				</div>
			</div>
			<div className="w3-col w3-container MyStckyHeader" style={{ width: "50%" }}>
				<div className="w3-card-4" style={{ height: "90vh", overflowY: "auto" }}>
					<header className="w3-container w3-light-blue">
						<h3><b>{canSignal.signalKey}</b></h3>
					</header>

					<div className="w3-container">
						<table className="w3-table">
							<tbody>
								<tr>
									<td><b>Key</b></td>
									<td><b>{canSignal.signalKey}</b></td>
								</tr>
								<tr>
									<td><b>CAN Signal</b></td>
									<td><b>{canSignal.signalName}</b></td>
								</tr>
								<tr>
									<td><b>Description</b></td>
									<td>{canSignal.description}</td>
								</tr>
								<tr>
									<td><b>Value Descriptions</b></td>
									<td>{getValueDescription(canSignal.valueDescAry)}</td>
								</tr>
								<tr>
									<td><b>Sender</b></td>
									<td>{getProperty("Sender")}</td>
								</tr>
								<tr>
									<td><b>Receivers</b></td>
									<td>{getReceivers()}</td>
								</tr>
								<tr>
									<td><b>Length</b></td>
									<td>{getProperty("Length")}</td>
								</tr>
								<tr>
									<td><b>Init Value</b></td>
									<td>{getProperty("Init Value")}</td>
								</tr>
								<tr>
									<td><b>Factor</b></td>
									<td>{getProperty("Factor")}</td>
								</tr>
								<tr>
									<td><b>Offset</b></td>
									<td>{getProperty("Offset")}</td>
								</tr>
								<tr>
									<td><b>Min</b></td>
									<td>{getProperty("Min")}</td>
								</tr>
								<tr>
									<td><b>Max</b></td>
									<td>{getProperty("Max")}</td>
								</tr>
								<tr>
									<td><b>Data Type</b></td>
									<td>{getProperty("Type")}</td>
								</tr>
								<tr>
									<td><b>Range</b></td>
									<td>{getRange()}</td>
								</tr>
								{canSignal.signalName && canSignal.signalName !== "-" && signalMap.has(canSignal.signalName) &&
									<tr>
										<td><b>Current Value</b></td>
										<td>
											<EcuSignal
												signal={signalMap.get(canSignal.signalName)}
											/>
										</td>
									</tr>
								}
							</tbody>
						</table>
					</div>
				</div>
			</div>
		</div>
	);
}

const EcuSignal = ({ signal }) => {
	const initPhysicalValue = signal.initValue;
	const initRawValue = physToRaw(initPhysicalValue, signal);
	const [physicalValue, setPhysicalValue] = useState(Number(initPhysicalValue));

	const setCanValue = (name, value) => {
		let valueToSend = value;
		if (signal.length <= 32) {
			valueToSend = Number(value);
		}
		
		const jsonData = {
			signals: [ 
				{ 
					name  : name, 
					value : valueToSend
				} 
			],
			type: SIGNAL_TYPES.RAW
		};
		
		setSignals(jsonData);
	}

	function setRawValue(value) {
		const rawVal = String(value).split(" : ")[0];
		let physVal = Number(rawVal) * Number(signal.factor) + Number(signal.offset);
		physVal = Number(physVal.toFixed(5));
		setCanValue(signal.name, physVal);
	}

	return (
		<>
			<input
				type="text" id={"can-value-text"}
				defaultValue={physicalValue}
				onChange={(e) => { setPhysicalValue(e.target.value); }}
				onKeyDown={(e) => {
					if (e.key === 'Enter') {
						setCanValue(signal.name, physicalValue);
					}
				}}
			>
			</input>
			<span>&emsp;</span>
			<span id={"can-raw-value-text"}>{initRawValue}</span>
			<span>&emsp;</span>
			{
			Object.keys(signal.valueDescAry).length > 0 &&
				<select 
					id={"can-value-combo"} 
					onChange={(e) => {setRawValue(e.target.value)}} 
				>
				{
					Object.keys(signal.valueDescAry).map((key) => 
						<option key={key}>
						{key.substring(5) + " : " + signal.valueDescAry[key]}
						</option>
					)
				}
				</select>
			}
		</>
	);
}

function updateCanField(signal, signalDef) {
	let tag = document.getElementById("can-value-text");
	tag.value = signal.value;

	const rawValue = String(physToRaw(signal.value, signalDef));
	
	tag = document.getElementById("can-raw-value-text");
	tag.innerText = rawValue;

	tag = document.getElementById("can-value-combo");
	if (tag) {
		const options = tag.getElementsByTagName("option");
		for (let i = 0; i < options.length; i++) {
			if (options[i].innerText.startsWith(rawValue)) {
				tag.selectedIndex = i;
				break;
			}
		}
	}
}

function physToRaw(physVal, signal) {
	let rawVal = physVal;
	if (signal.factor > 0) {
		rawVal -= signal.offset;
		rawVal /= signal.factor;
		rawVal = Math.round(rawVal.toString());
	}
	return rawVal;
}

